@import "../../assets/styles/base";
@font-face {
  font-family: "LuckiestGuy";
  font-weight: 400;
  font-style: normal;
  src: url("../../assets/fonts/Luckiest_Guy/LuckiestGuy-Regular.ttf")
    format("truetype");
}
.logo {
  display: inline-block;
  color: rgb(10, 59, 110);
  font-family: "LuckiestGuy";
  text-shadow: 1px 1px rgb(151, 88, 82), 2px 2px rgba(205, 245, 94, 0.836),
    3px 3px rgba(122, 94, 245, 0.836), 4px 4px rgba(110, 18, 95, 0.836),
    5px 5px rgba(205, 245, 94, 0.836);
}

.LogoInHeader {
  vertical-align: middle;
  font-size: $base_Header_Size;
  padding-bottom: 5px;
  @include mobile {
    font-size: $Mobile_Header_Size;
  }
}
