.Toolbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  top: 0;
  left: 0;

  padding: 5px 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  z-index: 90;
  vertical-align: middle;
}

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}
